<template>
  <header class="headerItem">
    <el-row :gutter="20">
      <div class="top">
        <el-col :span="6" :offset="4">
          <div class="top-left">
            <a href="https://segmentfault.com/">思否社区</a>
            <a href="https://gitee.com/">码云</a>
            <a href>帮助</a>
          </div>
        </el-col>
        <el-col :span="6" :offset="4">
          <div v-if="!username" class="top-right">
            <router-link to="/login">登录</router-link>
            <router-link to="/signup">注册</router-link>
          </div>
          <div v-else class="top-right">
            <el-dropdown @command="handleCommand">
              <span class="el-dropdown-link">
                {{username}}
                <i class="el-icon-arrow-down el-icon--right"></i>
              </span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">个人主页</el-dropdown-item>
                <el-dropdown-item divided command="b">注销</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-col>
      </div>
    </el-row>
    <div class="searchItem">
      <hr>
      <el-row :gutter="20">
        <el-col :span="10" :offset="7">
          <div class="search">
            <el-input
              size="large"
              placeholder="请输入"
              suffix-icon="el-icon-search"
              v-model="searchInput"
              @keyup.enter.native="funSearch"
            ></el-input>
            <!-- <p style="font-size:12px;color:#f8f8f9">TAGS:</p>
            <el-tag v-for="tag in tags" :key="tag.name" :type="tag.type">{{tag.name}}</el-tag>-->
          </div>
        </el-col>
      </el-row>

      <hr>
      <nav>
        <el-row :gutter="20">
          <el-col :span="8" :offset="7">
            <div class="container">
              <ul>
                <li v-if="entid">
                  <router-link to="/allvalue">
                    <strong>所有人选</strong>
                  </router-link>
                </li>
                <li v-else>
                  <router-link to="/allvalue">
                    <strong>所有职位</strong>
                  </router-link>
                </li>
                <li v-if="entid">
                  <router-link to="/entjob">
                    <strong>职位管理</strong>
                  </router-link>
                </li>
                <li v-else>
                  <router-link to="/recjob">
                    <strong>面试邀请</strong>
                  </router-link>
                </li>
                <li v-if="entid">
                  <router-link to="/entsentjob">
                    <strong>人才管理</strong>
                  </router-link>
                </li>
                <li v-else>
                  <router-link to="/sentjob">
                    <strong>我的投递</strong>
                  </router-link>
                </li>
                <li v-if="entid">
                  <router-link to="/addjob">
                    <strong>发布职位</strong>
                  </router-link>
                </li>
                <li v-else>
                  <router-link to="/resume">
                    <strong>我的简历</strong>
                  </router-link>
                </li>
              </ul>
            </div>
          </el-col>
        </el-row>
      </nav>
    </div>
  </header>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "headerItem",
  data() {
    return {
      psnid: "",
      entid: "",
      username: "",
      tags: [
        { name: "Java", type: "" },
        { name: "Python", type: "success" },
        { name: "React.js", type: "info" },
        { name: "Vue.js", type: "warning" },
        { name: ".NET", type: "danger" }
      ],
      searchInput: ""
    };
  },
  created() {
    if (Cookies.get("username")) {
      this.username = Cookies.get("username");
    }
    if (Cookies.get("psnid")) {
      this.entid = "";
      this.psnid = Cookies.get("psnid");
    } else if (Cookies.get("entid")) {
      this.psnid = "";
      this.entid = Cookies.get("entid");
    }
  },
  methods: {
    handleCommand(command) {
      if (command == "a") {
        if (Cookies.get("entid")) {
          this.entid = Cookies.get("entid");
          this.psnid = "";
          this.$router.push("/entmain");
        } else if (Cookies.get("psnid")) {
          this.psnid = Cookies.get("psnid");
          this.entid = "";
          this.$router.push("/resume");
        } else {
          this.$message({
            type: "danger",
            message: "身份认证已过期，请重新登陆"
          });
        }
      }
      if (command == "b") {
        this.axios.post("loginOut", {}).then(data => {
          if (data.data.status == "ok") {
            Cookies.remove("username");
            Cookies.remove("sessionid");
            if (Cookies.get("entid")) {
              Cookies.remove("entid");
            } else if (Cookies.get("psnid")) {
              Cookies.remove("psnid");
            }
            let NewPage = "_empty" + "?time=" + new Date().getTime() / 1000;
            this.$router.push(NewPage);
            this.$router.go(-1);
          }
        });
      }
    },
    funSearch() {
      this.$router.push({
        name: "allvalue",
        query: { item: this.searchInput }
      });
    }
  }
};
</script>
<style scoped>
header {
  background: #f8f8f9;
}
.top {
  height: 24px;
  width: 100%;
  padding-top: 8px;
}
.top-left {
  text-align: left;
}
.top-right {
  text-align: right;
}
.top-left a {
  padding: 0px 15px;
  font-size: 12px;
  text-decoration: none;
  color: #303133;
}
.top-right a {
  padding: 0px 10px;
  font-size: 12px;
  text-decoration: none;
  color: #303133;
}
.searchItem {
  /* background: url("../assets/tooopen_sy_191330099764.jpg") center no-repeat
    fixed; */

  background: url("../assets/body-bg.png") center no-repeat fixed;
  background-size: cover;
}
.search {
  padding: 60px;
}
nav {
  width: 100%;
  height: 60px;
  /* background: #67c23a; */
}
.container ul {
  list-style-type: none;
  padding: 0;
  margin: 20px 0;
}
.container ul li {
  display: inline-block;
  margin: 0px 10px;
}
.container ul li span {
  color: #f8f8f9;
  font-size: 16px;
}
.container ul li a {
  text-decoration: none;
  color: #f8f8f9;
  font-size: 16px;
}
h3 {
  margin: 40px 0 0;
}
</style>
