<template>
  <div class="showBlogsItem">
    <div class="content" v-if="entid">
      <el-row :gutter="20">
        <el-col :span="8" :offset="8">
          <div class="title">热门人选</div>
        </el-col>
      </el-row>
      <div v-for="(psn,index) in psnList" :key="index" class="singleItem">
        <el-row :gutter="20">
          <el-col :span="12" :offset="6">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <router-link
                  :to="{name: 'singlepsn', query:{resumeid: psn.resumeid,psnid:psn.psnid}}"
                  target="_blank"
                >
                  {{psn.name}}
                  <span class="update-time">({{psn.updateTime}})</span>
                  <span class="ent-name">{{psn.nowStatus}}</span>
                  <p class="psn-item">
                    <span class="pay">{{psn.jobPay}}</span>|
                    <span>{{psn.workExp}}</span>|
                    <span>{{psn.jobAdd}}</span>|
                    <span>{{psn.degree}}</span>
                  </p>
                  <p class="psn-item">
                    <span>期望职位:{{psn.jobName}}</span>
                  </p>
                </router-link>
              </div>
              <div>
                <el-tag type="success" v-for="(tag,index) in psn.psnTag" :key="index">{{tag}}</el-tag>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6">
          <div class="btn">
            <router-link to="/allvalue">
              <el-button type="success" round>更多</el-button>
            </router-link>
          </div>
        </el-col>
      </el-row>
    </div>
    <div class="content" v-else>
      <el-row :gutter="20">
        <el-col :span="8" :offset="8">
          <div class="title">热门招聘</div>
        </el-col>
      </el-row>
      <div v-for="job in jobList" :key="job.jobid" class="singleItem">
        <el-row :gutter="20">
          <el-col :span="12" :offset="6">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <router-link :to="{name: 'singlejob', query:{jobid: job.jobid}}" target="_blank">
                  <span>
                    {{job.jobName}}
                    <span class="update-time">({{job.updateTime}})</span>
                  </span>
                  <span class="ent-name">{{job.entName}}</span>
                  <br>
                  <span class="pay">{{job.jobPay}}</span>|
                  <span>{{job.degree}}</span>|
                  <span>{{job.workExp}}</span>
                </router-link>
              </div>
              <div>
                <el-tag type="success" v-for="(tag,index) in job.entTag" :key="index">{{tag}}</el-tag>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <el-row :gutter="20">
        <el-col :span="12" :offset="6">
          <div class="btn">
            <router-link to="/allvalue">
              <el-button type="success" round>更多</el-button>
            </router-link>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "showBlogsItem",
  data() {
    return {
      psnid: "",
      entid: "",
      jobList: [
        {
          jobid: "",
          jobName: "",
          jobPay: "",
          workExp: "",
          jobType: "",
          entTag: []
        }
      ],
      psnList: [],
      searchItem: {}
    };
  },
  created() {
    if (Cookies.get("entid")) {
      this.psnid = "";
      this.entid = Cookies.get("entid");
      this.axios
        .post("searchPsn", {
          key: "0000",
          type: "all"
        })
        .then(data => {
          if (data.data.status == "ok") {
            let arr = JSON.parse(data.data.msg);
            let res = [];
            for (let key in arr) {
              res[key] = arr[key].fields;
              res[key].resumeid = arr[key].pk;
              res[key].psnTag = res[key].psnTag.split(",");
              switch (res[key].nowStatus) {
                case "0":
                  res[key].nowStatus = "待沟通";
                  break;
                case "1":
                  res[key].nowStatus = "学生";
                  break;
                case "2":
                  res[key].nowStatus = "观望机会";
                  break;
                case "3":
                  res[key].nowStatus = "求职中";
                  break;
                case "4":
                  res[key].nowStatus = "暂不考虑";
                  break;
              }
              // res[key].resumeid = arr[key].pk;
            }
            this.psnList = res.slice(0, 10);
          }
        });
    } else {
      this.entid = "";
      this.psnid = Cookies.get("psnid");
      this.axios
        .post("searchJob", {
          key: "0000",
          type: "all"
        })
        .then(data => {
          if (data.data.status == "ok") {
            let arr = JSON.parse(data.data.msg);
            let res = [];
            for (let key in arr) {
              res[key] = arr[key].fields;
              res[key].jobid = arr[key].pk;
              res[key].entTag = res[key].entTag.split(",");
            }
            this.jobList = res.slice(0, 10);
          }
        });
    }
  }
};
</script>
<style scoped>
.showBlogsItem {
  background: #f8f8f9;
}
.content {
  padding: 60px;
  color: #303133;
}
.content .title {
  color: #303133;
  text-align: center;
  font-size: 18px;
  text-decoration: #303133;
}
.singleItem {
  margin: 30px 0;
}
.singleItem a {
  color: #303133;
  text-decoration: none;
}
p {
  font-size: 14px;
}
.btn {
  text-align: right;
}
.update-time {
  color: #909399;
  font-size: 14px;
}
.pay {
  color: #f56c6c;
}
.ent-name {
  float: right;
}
.el-tag {
  margin: 10px;
}
.psn-item {
  margin: 10px 0;
}
</style>
